          "id": "3",
          "icon": "el-icon-menu",
          "path": "/other/task",
        },
        {
          "name": "事件管理",
          "id": "4",
          "icon": "el-icon-setting",
          "path": "/other/events",
        },
        {
          "name": "键值管理",
          "id": "5",
          "icon": "el-icon-setting",
          "path": "/other/kvManage",
        },


      ],
    })

    const selectMenu = (index: any, indexPath: any) => {
