
import { defineComponent, reactive, toRefs } from 'vue'
import {ElMessage} from "element-plus";

export default defineComponent({
  components: {
  },
  created() {
    this.getConfig()
  },
  setup() {
    const axios = require('axios');

    const state = reactive({
      form: {
        CONTENT_TYPE: '',
        USER_AGENT: '',
        X_XSRF_TOKEN: '',
        X_XSRF_HAITANG: '',
        COOKIE: '',
        UM_KEY_MASTER: '',
        UM_KEY_SLAVES: [],
      },
    })

    // 保存/更新 主机
    const addOrUpdateKv = () => {
      console.log(JSON.stringify(state.form))

      axios.post('http://127.0.0.1:8000/api/save_config', JSON.stringify(state.form))
          .then((response:any) => {
            const res = response.data;
            let msgType: any = res.code === 200? 'success' : 'error'
            ElMessage({
                showClose: true,
                message: res.msg,
                type: msgType,
              })
            console.log(res)
          })
    }

    const getConfig = () => {
      axios.get('http://127.0.0.1:8000/api/get_config')
          .then((response:any) => {
            const res = response.data;
            if (res.code === 200) {
              ElMessage({
                showClose: true,
                message: 'getConfig Succeed.',
                type: 'success',
              })

              // 显示列表
              state.form = res.data

            } else {
              ElMessage({
                showClose: true,
                message: 'getConfig Fail：' + res.msg,
                type: 'error',
              })
            }
            console.log(res)
          })
    }

    return {
      ...toRefs(state),
      addOrUpdateKv,
      getConfig,
    }
  },
})

