
import { defineComponent, reactive, toRefs } from 'vue'
import {ElMessage} from "element-plus";
import {saveAs} from "file-saver";
export default defineComponent({
  created() {
    this.setDefaultFilterFrom()
    this.getUmKeys()
  },
  setup() {
    const axios = require('axios');

    const state = reactive({

      filterForm: {},

      showDrawer: false,

      umKeys:[],

      fileList:[],

      tableData: [],
      dialogFormVisible: false,
      form: {
        um_key: '',
        um_eventId: '',
        um_name: '',
        um_displayName: '',
        um_status: '',
        um_eventType: '',
        um_eventType_int: 0, //（multiattribute=0 ;  calculation=1）
        um_countToday: 0,
        um_countYesterday: 0,
        um_deviceYesterday: 0,
      },
      query: {
        um_key: '',
        refresh: 0,
        pg_index: 1,
        pg_size: 15,
      },
      total: 50,
      formLabelWidth: '120px',
      dialogCommitTitle: '添加',
    })

    // 设置默认的筛选view
    const setDefaultFilterFrom =()=>{
      state.filterForm = {
        keyword: '',
        sort_by: 'deviceYesterday',
        sort: 'desc',
        state: '',
        type: '1',
      }
    }

    // 获取友盟key列表
    const getUmKeys = () => {
      axios.get('http://127.0.0.1:8000/api/get_um_keys')
          .then((response:any) => {
            const res = response.data;
            if (res.code === 200) {
              // 显示
              if(res.data.length > 0){
                state.umKeys = res.data
                state.query.um_key = res.data[0].um_key
                getUmEvents()
              }
            } else {
              ElMessage({
                showClose: true,
                message: 'getUmKeys Fail：' + res.msg,
                type: 'error',
              })
            }
            console.log(res)
          })
    }

    // 获取事件列表
    const getUmEvents = () => {
      axios.post('http://127.0.0.1:8000/api/um_event', JSON.stringify(state.query))
          .then((response:any) => {
            const res = response.data;
            if (res.code === 200) {
              ElMessage({
                showClose: true,
                message: 'getUmEvents Succeed.',
                type: 'success',
              })

              // 显示列表
              state.tableData = res.data.lst
              state.total = res.data.total
              state.query.refresh = 0

            } else {
              ElMessage({
                showClose: true,
                message: 'getUmEvents Fail：' + res.msg,
                type: 'error',
              })
            }
            console.log(res)
          })
    }

    // 编辑
    const editHost = (index: any, row: any) => {
      console.log(index, row)
      state.form = {
        um_key: row.um_key,
        um_eventId: row.um_eventId,
        um_name: row.um_name,
        um_displayName: row.um_displayName,
        um_status: row.um_status,
        um_eventType: row.um_eventType,
        um_eventType_int: row.um_eventType_int,
        um_countToday: row.um_countToday,
        um_countYesterday: row.um_countYesterday,
        um_deviceYesterday: row.um_deviceYesterday,
      }
      state.dialogFormVisible = true
      state.dialogCommitTitle = "更新"
    }

    // 删除
    const deleteHost = (index: any, row: any) => {
      console.log(index, row)
    }

    // 批量暂停
    const doStop = () => {
      console.log("批量暂停")
    }

    // 添加或更新单条事件
    const addOrUpdateEvent = () => {
      console.log("添加或更新单条事件")
    }

    // 导导入自定义事件
    const importEvents = () => {
      console.log("导导入自定义事件")
    }

    // 导出所有自定义事件
    const exportEvents = () => {
      axios.post('http://127.0.0.1:8000/api/um_event_export', JSON.stringify(state.query))
          .then((response:any) => {
            const res = response.data;
            if (res.code === 200) {
              // 显示列表
              let str = new Blob([res.data], {type: 'text/plain;charset=utf-8'});
              // 注意：这里要手动写上文件的后缀名
              saveAs(str, `友盟自定义事件_${state.query.um_key}.txt`);
            } else {
              ElMessage({
                showClose: true,
                message: 'getUmEvents Fail：' + res.msg,
                type: 'error',
              })
            }
            console.log(res)
          })
    }

    // 分页当前页改变的监听
    const onCurrentPageChange = (index: any) => {
      console.log(`onCurrentPageChange ${index}`)
      state.query.pg_index = index
      getUmEvents()
    }

    // 分页size改变监听
    const onPageSizeChange = (size: any) => {
      console.log(`onPageSizeChange ${size}`)
      state.query.pg_index = 1
      state.query.pg_size = size
      getUmEvents()
    }

    // 文件上传预备
    const handlePreview = (file: any) => {
      console.log(`handlePreview ${file}`)
    }

    // 文件上传成
    const handleUploadSucceed = (response: any, file: any, file_list: any) => {
      state.dialogFormVisible = false
      console.log(`handleUploadSucceed ${response }${file} ${file_list}`)
      ElMessage({
                showClose: true,
                message: '上传成功',
                type: 'success',
              })
    }

    // 友盟key选中状态监听
    const onUmKeyChange = (um_key: any) => {
      console.log(`onUmKeyChange ${um_key}`)
      state.query.pg_index = 1
      getUmEvents()
    }

    // 筛选view重置监听
    const onFilterReset = () => {
      console.log(`onFilterReset`)
      setDefaultFilterFrom()
    }

    // 筛选view关闭（提交）监听
    const onFilterSubmit = () => {
      console.log(`onFilterSubmit ${state.filterForm}`)
      console.log(state.filterForm)
      state.showDrawer = false
    }

    return {
      ...toRefs(state),
      getUmKeys,
      getUmEvents,

      editHost,
      deleteHost,
      doStop,
      addOrUpdateEvent,
      onCurrentPageChange,
      onPageSizeChange,
      exportEvents,
      importEvents,

      handlePreview,
      handleUploadSucceed,

      onUmKeyChange,

      onFilterSubmit,
      onFilterReset,
      setDefaultFilterFrom,
    }
  },
})

